import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


import { UserMoveinoutTrackerComponent } from './user-moveinout-tracker.component';
import { UserMoveinComponent } from '../../shared/components/user-movein/user-movein.component';
import { UserMoveoutComponent } from '../../shared/components/user-moveout/user-moveout.component';
import { MoveinHistoryComponent } from 'src/app/shared/components/movein-history/movein-history.component';

const routes: Routes = [
	{ path: '', redirectTo: 'movein', pathMatch: 'full'},
	{ path: 'movein', component: UserMoveinComponent },
	{ path: 'moveout', component: UserMoveoutComponent },
	{ path: 'history', component: MoveinHistoryComponent },
	{ path: '**', redirectTo: 'movein', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class UserMoveinoutTrackerRoutingModule { }
